import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    aboutInfo: [],
    projectsInfo: []
  },
  mutations: {
    SET_ABOUT_INFO(state, aboutInfo) {
      state.aboutInfo = aboutInfo;
    },
    SET_PROJECTS_INFO(state, projectsInfo) {
      state.projectsInfo = projectsInfo;
    }
  },
  actions: {
    fetchAboutData({dispatch}) {
      // The place where you do the async calls to backend
      return new Promise(resolve => {
        setTimeout(async () => {
          const res_about = await fetch("data/about.json"); // Assume nothing goes wrong since local
          const val_about = await res_about.json();

          // Manually parse double moustache
          for (let i = 0; i < val_about.length; i++) {
            val_about[i].text = val_about[i].text.replace(
              "{{age}}",
              await dispatch("getAge")
            );
          }
          resolve(val_about);
        });
      });
    },
    async fetchAbout({dispatch, commit}) {
      const aboutJson = await dispatch("fetchAboutData");
      commit("SET_ABOUT_INFO", aboutJson);
    },
    fetchProjectsData() {
      // The place where you do the async calls to backend
      return new Promise(resolve => {
        setTimeout(async () => {
          const res_projects = await fetch("data/projects.json");
          const val_projects = await res_projects.json();
          resolve(val_projects);
        });
      });
    },
    async fetchProjects({dispatch, commit}) {
      const projectsJson = await dispatch("fetchProjectsData");
      commit("SET_PROJECTS_INFO", projectsJson);
    },
    async search({commit, dispatch}, {text}) {
      const myJson = await dispatch("fetchProjectsData"); // To get the most recent from server
      const keywords = text
        .toLowerCase()
        .split(/[^a-z]/)
        .filter(el => el.length > 0);
      if (keywords.length > 0) {
        const values = myJson.filter(
          val =>
            keywords.some(el => val.title.toLowerCase().includes(el)) |
            keywords.some(el => val.text.toLowerCase().includes(el)) |
            keywords.some(el => val.keywords.toLowerCase().includes(el))
        );
        if (values.length > 0) {
          commit("SET_PROJECTS_INFO", values);
        } else {
          const no_info = [
            {
              title: "Under Construction",
              text: "No projects found for the requested keywords, but I'm all ears if you have any suggestions!",
              keywords: text,
              date: "20??-??-??",
              url: "https://github.com/RubenPants",
              image: "img/projects/construction.png"
            }
          ];
          commit("SET_PROJECTS_INFO", no_info);
        }
      } else {
        commit("SET_PROJECTS_INFO", myJson);
      }
    },
    getAge() {
      const birthday = +new Date("1997-05-15");
      return ~~((Date.now() - birthday) / 31557600000);
    }
  },
  getters: {
    projectsInfo(state) {
      return state.projectsInfo;
    },
    aboutInfo(state) {
      return state.aboutInfo;
    }
  },
  modules: {}
});
