<template>
  <div>
    <b-container>
      <CarouselCard :interval="0" height="600px" arrow="hover" class="carousel">
        <CarouselCardItem v-for="info in aboutInfo" :key="info.title">
          <card
            :image_path="info.image"
            :title="info.title"
            :description="info.text"
            :windowWidth="windowWidth"
          ></card>
        </CarouselCardItem>
      </CarouselCard>
    </b-container>
  </div>
</template>

<script>
import CardAbout from "@/components/CardAbout.vue";
import { CarouselCard, CarouselCardItem } from "vue-carousel-card";
import "vue-carousel-card/styles/index.css";
import { mapGetters } from "vuex";

export default {
  components: {
    CarouselCard,
    CarouselCardItem,
    card: CardAbout
  },
  computed: {
    ...mapGetters(["aboutInfo"])
  },
  data() {
    return {
      windowWidth: window.innerWidth
    };
  },
  mounted() {
    this.fetchData();
    window.addEventListener("resize", () => {
      this.windowWidth = window.innerWidth;
    });
  },
  methods: {
    async fetchData() {
      await this.$store.dispatch("fetchAbout");
    }
  }
};
</script>

<style lang="scss">
.carousel-card-indicator.is-active button {
  background-color: #000a12;
}
.carousel-card-indicator button {
  background-color: #000a12;
}

.carousel {
  padding: 10px;
}
</style>
