<template>
  <div class="card-about">
    <!-- Desktop -->
    <b-row class="card-about-inner" v-if="windowWidth >= 768">
      <b-col class="image-about" cols="3">
        <imageComponent :width="windowWidth" :image_path="image_path" />
      </b-col>
      <b-col>
        <!--Takes the rest of the columns-->
        <div class="title-about">{{ title }}</div>
        <scrollbar suppressScrollX="true">
          <div class="description-about" v-html="description"></div>
        </scrollbar>
      </b-col>
    </b-row>

    <!-- Mobile -->
    <b-row class="card-about-inner" v-else>
      <b-col>
        <b-row class="image-about-mobile">
          <imageComponent :width="windowWidth" :image_path="image_path" />
        </b-row>
        <div class="title-about-mobile">
          {{ title }}
        </div>
        <scrollbar suppressScrollX="true">
          <div class="description-about-mobile" v-html="description"></div>
        </scrollbar>
      </b-col>
    </b-row>
  </div>
</template>

<script>
import ImageComponent from "@/components/ImageComponent.vue";
import vueCustomScrollbar from "vue-custom-scrollbar";

export default {
  name: "CardAbout",
  components: {
    imageComponent: ImageComponent,
    scrollbar: vueCustomScrollbar
  },
  props: ["image_path", "title", "description", "windowWidth"]
};
</script>

<style lang="scss">
// General
.col {
  padding: 0;
}

.card-about {
  height: 100%;
  width: 100%;
  padding: 10px;
  background: #263238;
  border-radius: 10px;
}

.card-about-inner {
  height: 100%;
  width: 100%;
  margin: 0;
  background: #eceff1;
  overflow: hidden;
}

// Image related
.image-about {
  overflow: hidden;
}

.image-about-mobile {
  @extend .image-about;
  height: 250px;
}

// Title related
.title-about {
  font-weight: bold;
  padding: 16px;
  font-size: 3em;
  height: 100px;
}

.title-about-mobile {
  @extend .title-about;
  padding-top: 8px;
  padding-bottom: 8px;
  font-size: 2.5em;
  height: 70px;
}

// Description related
.description-about {
  margin: 0;
  padding-left: 16px;
  padding-right: 16px;
  font-size: 1.2em;
  width: 100%;
  height: 480px; // 600 - 100 (title) - 20 (padding)
}

.description-about-mobile {
  @extend .description-about;
  font-size: 1em;
  height: 260px; // 600 - 250 (image) - 70 (title) - 20 (padding)
}
</style>
