<template>
  <div>
    <b-navbar
      toggleable="lg"
      type="dark"
      variant="dark"
      fixed="top"
      class="mb-0"
    >
      <b-navbar-brand tag="h1" class="mb-0">
        RubenPants
      </b-navbar-brand>

      <b-navbar-toggle target="nav-collapse" />

      <b-collapse id="nav-collapse" is-nav>
        <b-navbar-nav>
          <b-nav-item to="/">About Me</b-nav-item>
          <b-nav-item to="/projects">Projects</b-nav-item>
          <b-nav-item href="./data/cv_ruben_broekx.pdf" target="_blank">
            Curriculum Vitae
          </b-nav-item>
        </b-navbar-nav>

        <!-- Right aligned nav items -->
        <b-navbar-nav class="ml-auto" v-if="inProject">
          <!-- Already start searching during typing -->
          <b-nav-form @submit.prevent="search" @keyup.prevent="search">
            <b-icon-info-circle
              class="h4 info-icon"
              v-b-tooltip.hover
              title="Search multiple keywords by splitting them via a comma"
            />
            <b-form-input
              size="sm"
              class="mr-sm-2 input-field"
              placeholder="Search..."
              v-model="searchText"
            />
            <b-button size="sm" class="my-2 my-sm-0" type="submit">
              Search
            </b-button>
          </b-nav-form>
        </b-navbar-nav>
      </b-collapse>
    </b-navbar>
  </div>
</template>

<script>
import { BIconInfoCircle } from "bootstrap-vue";

export default {
  name: "Header",
  components: {
    BIconInfoCircle
  },
  methods: {
    search() {
      this.$store.dispatch("search", { text: this.searchText });
    }
  },
  data() {
    return {
      searchText: "",
      inProject: false
    };
  },
  watch: {
    $route(to) {
      this.inProject = to.name.toLowerCase().includes("projects");
    }
  }
};
</script>

<style lang="scss">
#nav {
  height: 56px; // Navbar default

  // Keep above &.router-link-exact-active since this overwrites the hover
  a:hover {
    color: #cfd8dc;
  }

  a {
    font-weight: bold;
    color: white;

    &.router-link-exact-active {
      color: #7b888f;
    }
  }

  .bg-dark {
    background-color: #263238 !important;
  }
}

.navbar-brand {
  pointer-events: none;
  font-weight: bold;
  color: white;
}

.info-icon {
  color: #cfd8dc;
  margin: 8px;
}

// Mobile
.navbar-collapse {
  text-align: start;
}

.form-inline {
  width: 100%;
  flex-flow: row;
}

.input-field {
  margin-right: 8px;
}
</style>
